<template>
  <div id="app">
    <div class="flex justify-between border-b p-3">
      <div class="flex items-center border-b">
        <div class="px-2 ">
          <input class="outline-none text-sm" placeholder="搜索" v-model="keyword"/>
        </div>
        <div @click="getList()" class="text-xs cursor-pointer">搜索</div>
      </div>
      <div class="flex items-center">
        <div class="border h-8 flex-1 px-2 flex items-center">
          <input @click="showFileInput()" class="outline-none text-sm" placeholder="请选择文件" v-model="name"/>
        </div>
        <input class="hidden" type="file" ref="file" @change="getFile($event)">
        <div @click="addFile()"
             class="bg-blue-300 text-xs h-8 w-11 flex items-center justify-center text-white cursor-pointer">
          上传
        </div>
      </div>
    </div>
    <div class="py-3">
      <div @click="clickItem(item.url)" v-for="(item, index) in list" :key="index" class="p-3 float-l">
        <div class="bg-cover bg-no-repeat bg-center w-32 h-32 rounded border flex items-end"
             :style="'background-image: url('+item.url+')'">
          <div class="text-xs truncate py-1 text-white px-2 bg-black bg-opacity-30 w-full">
            {{ item.name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      baseURL: process.env.NODE_ENV === "production" ? "/api/v1/file" : "http://www.api.local/api/v1/file",
      list: [],
      keyword: '',
      name: '',
      file: {},
    }
  },
  mounted() {
    this.getList();
  },
  methods: {
    clickItem(url) {
      window.parent.postMessage({
        method: "onChange",
        content: url
      }, "*");
    },
    showFileInput() {
      this.$refs.file.click();
    },
    getFile(event) {
      if (event.target.files.length > 0) {
        this.file = event.target.files[0];
        this.name = this.file.name.replace(/\.\w+$/, '');
      }
    },
    getList() {
      this.$http.get('/file?keyword=' + this.keyword, {baseURL: this.baseURL}).then((res) => {
        this.list = res.data.data.data;
      })
    },
    addFile() {
      if (!this.file.name || !this.name) {
        return false;
      }
      let formData = new FormData();
      formData.append('name', this.name);
      formData.append('file', this.file);
      this.$http.post('/file', formData, {baseURL: this.baseURL}).then(() => {
        this.getList();
        this.file = {};
        this.name = '';
      })
    },
    saveFile(id) {
      this.$http.post('/file/' + id, {title: this.title}, {baseURL: this.baseURL}).then(() => {
        this.getList();
      })
    },
    delFile(id) {
      this.$http.delete('/file/' + id, {baseURL: this.baseURL}).then(() => {
        this.getList();
      })
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
